import React, { useState } from "react";
import Header from "./Header";
import Footer from "./Footer";
import Note from "./Note"
import NewNote from "./NewNote";


function App() {

    const [Notas, setNotes] = useState([{
        noteTitle: "Bienvenido",
        noteContent: "Para crear una nota, introduzcala en la caja superior!"
    }]);

    function handleNuevaNota(note) {
        setNotes(prevValue => {
            return [...prevValue, note];
        });
    }

    function handleBorrarNota(id) {
        setNotes(prevValue => {
            return prevValue.filter((Nota, index) => {
                return index !== id;
            })
        })

    }

    return (
        <div>
            <Header />
            <NewNote funcion={handleNuevaNota} />
            {Notas.map((Notes, index) => (
                <Note key={index} id={index} title={Notes.noteTitle} content={Notes.noteContent} funcion={handleBorrarNota} />
            ))}
            <Footer />
        </div>);
}

export default App;