import React, { useState } from "react";
import AddIcon from '@material-ui/icons/Add';
import Fab from '@material-ui/core/Fab';
import Zoom from '@material-ui/core/Zoom';
function NewNote(props) {
    const [isExpanded, setExpanded] = useState(false);

    const [note, setNote] = useState({
        noteTitle: "",
        noteContent: ""
    });

    function handleChange(event) {
        const { name, value } = event.target;

        setNote(prevValue => {
            return {
                ...prevValue,
                [name]: value
            }
        })
    }
    function handleClick(event) {
        props.funcion(note);

        setNote({
            noteTitle: "",
            noteContent: ""
        });

        event.preventDefault();
    }

    function Expand() {
        setExpanded(true);
    }

    return (<div>
        <form className="create-note" >
            {isExpanded && <input name="noteTitle" value={note.noteTitle} placeholder="Titulo" onChange={handleChange} ></input>}

            <input name="noteContent" onClick={Expand} value={note.noteContent} placeholder="Anotar..." onChange={handleChange} rows={isExpanded ? 3 : 1} ></input>
            <Zoom in={isExpanded}>
                <Fab type="submit" onClick={handleClick}>
                    <AddIcon />
                </Fab>
            </Zoom>
        </form>
    </div>);
}


export default NewNote;